<template>
    <yd-layout>
        <div class="mainContentQuery">
            <div class="contentSradius querySradius">
                <yd-cell-item>
                    <input slot="right" type="text" readonly="true" v-on:click="bottomPopup = true;popupArr=fromArr;statusRadio=statusRadioFrom;isType=0" :value='fromOpt' placeholder="请选择发车地点">
                </yd-cell-item>
                <yd-cell-item>
                    <input slot="right" type="text" readonly="true" v-on:click="bottomPopup = true;popupArr=toArr;statusRadio=statusRadioTo;isType=1" :value="toOpt" placeholder="请选择到达地点">
                </yd-cell-item>
                <img src="../../../static/images/query/turn@2x.png" class="resetBtn" v-on:click="change" />
            </div>
            <yd-button size="large" type="primary" bgcolor="#59a5fd" color="#fefefe" @click.native="queryBtn()">搜 索</yd-button>
        </div>
        <div class="mainContentFluid">
            <div class="subTitle">
                <yd-flexbox>
                    <yd-flexbox-item>
                        <div class="busList">班车时刻表</div>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <div class="map" @click="toggleNum=4" v-show="toggleNum!=4">查看备注</div>
                        <div class="map" @click="togglePage()" v-show="toggleNum==4">关闭备注</div>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="mainContentFluid queryPath" v-if="toggleNum == 1">
            <yd-flexbox v-for="(bus,index) in busList" :key="index">
                <div class="time" v-text="bus.time" v-show="bus.time.indexOf('-')==-1"></div>
                <div class="time xxTime" v-show="bus.time.indexOf('-')!=-1">
                    <div class="time1">{{bus.time.split('-')[0]}}</div>
                    <div class="time2">{{bus.time.split('-')[1]}}</div>
                </div>
                <div v-show="1==isKuohao(bus.to)">
                    <div class="add">{{bus.from}} <img class="roadPhoto" src="../../../static/images/query/arrow@2x.png" /> {{bus.to}}</div>
                </div>
                <div v-show="2==isKuohao(bus.to)">
                    <div class="add">{{bus.from}} <img class="roadPhoto" src="../../../static/images/query/arrow@2x.png" /> {{getAddress(bus.to, 0)}}</div>
                    <div class="addressRemark">（{{getAddress(bus.to, 1)}}</div>
                </div>
                <div class="biaoqian" v-show="bus.timeType.indexOf('双休')==-1">工作日</div>
                <div class="biaoqian weekBus" v-show="bus.timeType.indexOf('双休')!=-1">节假日</div>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid queryPath" v-if="toggleNum == 2">
            <yd-flexbox v-for="(bus,index) in searchList" :key="index">
                <div class="time" v-text="bus.time" v-show="bus.time.indexOf('-')==-1"></div>
                <div class="time xxTime" v-show="bus.time.indexOf('-')!=-1">
                    <div class="time1">{{bus.time.split('-')[0]}}</div>
                    <div class="time2">{{bus.time.split('-')[1]}}</div>
                </div>
                <div v-show="1==isKuohao(bus.to)">
                    <div class="add">{{bus.from}} <img class="roadPhoto" src="../../../static/images/query/arrow@2x.png" /> {{bus.to}}</div>
                </div>
                <div v-show="2==isKuohao(bus.to)">
                    <div class="add">{{bus.from}} <img class="roadPhoto" src="../../../static/images/query/arrow@2x.png" /> {{getAddress(bus.to, 0)}}</div>
                    <div class="addressRemark">（{{getAddress(bus.to, 1)}}</div>
                </div>
                <div class="biaoqian" v-show="bus.timeType.indexOf('双休')==-1">工作日</div>
                <div class="biaoqian weekBus" v-show="bus.timeType.indexOf('双休')!=-1">双休日</div>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid queryPath" v-if="toggleNum == 3">
            <div class="noResult">无搜索结果</div>
        </div>
        <div class="mainContentFluid" v-if="toggleNum == 4">
            <div class="fluidWidth remarkText" v-html='remarkObj'>
            </div>
        </div>
        <yd-popup v-model="bottomPopup" position="bottom" height="4rem" v-show="bottomPopup">
            <div class="btnPopup" slot="top">
                <div class="cancel" v-on:click="bottomPopup = false;statusRadio=999">取消</div>
                <div class="confirm" @click="confirm">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;isType==0?fromOpt=item:toOpt=item;isType == 0?statusRadioFrom =statusRadio :statusRadioTo =statusRadio" v-text='item'></li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import $ from '../../jquery-vendor.js';
export default {
    name: 'mcc',
    data() {
        return {
            btnActive: 1,
            statusRadio: 999,
            statusRadioFrom: 999,
            statusRadioTo: 999,
            fromOpt: '',
            toOpt: '',
            isType: 0,
            popupArr: [],
            fromArr: [],
            toArr: [],
            busList: [],
            searchList: [],
            bottomPopup: false,
            toggleNum: 1,
            remarkObj: ''
        }
    },
    mounted() {
        document.setTitle('班车查询');
        this.init();
    },
    methods: {
        confirm: function (event) {
            var vm = this;
            vm.bottomPopup = false;
            vm.isType == 0 ? vm.statusRadioFrom = vm.statusRadio : vm.statusRadioTo = vm.statusRadio;
        },
        init() {
            var vm = this;
            vm.xml(vm.conf.wcm + '/app/bus/index.xml', {},
                function (res) {
                    vm.remarkObj = $(res).find('remark').text();
                    var jsonObj = $.parseJSON($(res).find('json').text());
                    var data = jsonObj.jsonData;
                    vm.busList = data;
                    console.log(vm.busList);
                    vm.fromArr = jsonObj.fromArr;
                    vm.toArr = jsonObj.toArr;
                });
        },
        queryBtn() {
            let vm = this;
            if (vm.fromOpt == '') {
                vm.$dialog.toast({
                    mes: '请选择发车地点',
                    timeout: 1000
                });
            } else if (vm.toOpt == '') {
                vm.$dialog.toast({
                    mes: '请选择到达地点',
                    timeout: 1000
                });
            } else {
                vm.toggleNum = 2;
                vm.searchList = [];
                console.log(vm.toggleNum);
                for (var i = 0; i < vm.busList.length; i++) {
                    var item = vm.busList[i];
                    if (item.from.indexOf(vm.fromOpt) != -1 && item.to.indexOf(vm.toOpt) != -1) {
                        vm.searchList.push(item);
                    }
                }
                if (vm.searchList.length == 0) {
                    vm.toggleNum = 3;
                }
            }
        },
        change: function () {
            var vm = this;
            var temp = vm.fromOpt;
            vm.fromOpt = vm.toOpt;
            vm.toOpt = temp;
        },
        isKuohao(to) {
            if (to.indexOf('（') != -1) {
                return 2;
            } else {
                return 1;
            }
        },
        getAddress(to, num) {
            if (num == 0) {
                return to.split('（')[0];
            } else if (num == 1) {
                return to.split('（')[1];
            }
        },
        togglePage() {
            let vm = this;
            if (vm.searchList.length != 0) {
                vm.toggleNum = 2;
            } else {
                vm.toggleNum = 1;
            }
        }
    }
}
</script>
<style scoped>
.resetBtn {
    width: .7rem;
    height: .7rem;
    position: absolute;
    right: 0.3rem;
    top: .6rem;
}

.busList {
    font-size: .36rem;
    float: left;
    padding-left: .2rem;
    color: #333;
}

.busList,
.map {
    height: .83rem;
    line-height: .83rem;
}

.map {
    float: right;
    font-size: .28rem;
    border: 1px solid #59a5fd;
    border-radius: 5px;
    width: 1.5rem;
    text-align: center;
    height: .5rem;
    line-height: .5rem;
    color: #59a5fd;
}

.subTitle {
    /*height: 2.47rem;*/
    padding: 0 .24rem;
}

.btn,
.btn2 {
    width: 2.11rem;
    height: .53rem;
    border: 1px solid #E1DFDB;
    background: #ffffff;
    color: #666666;
    line-height: .53rem;
}

.btn2 {
    width: 3.72rem;
}

.btnactive {
    color: #ffffff;
    background: #00B6FC;
    border: 0;
}

.subTitle .yd-flexbox-item:nth-child(2) .btn {
    margin-left: .3rem;
}

.subTitle .yd-flexbox-item:nth-child(3) .btn {
    margin-left: .3rem;
}

.subTitle .yd-flexbox:nth-child(2) .btn {
    margin-top: 0;
}

.yd-btn-block {
    margin-top: .25rem;
    height: .8rem;
    font-size: .34rem;
}

.mapImg {
    width: 1.47rem;
    display: none;
    margin-top: .2rem
}

.via {
    width: .74rem;
    position: absolute;
    top: 0;
    right: 0;
}

.time {
    font-size: .38rem;
    color: #35a4ff;
    padding-left: .49rem;
    width: 1.8rem;
    line-height: 1.13rem;
}

.add {
    color: #666;
    font-size: .32rem;
    width: 3.6rem;
    padding-left: .2rem;
    line-height: 1.13rem;
}

.biaoqian {
    font-size: .28rem;
    color: #fff;
    background-color: #A4Ceff;
    width: 1.2rem;
    height: .5rem;
    line-height: .5rem;
    text-align: center;
    border-radius: 4px;
    position: absolute;
    right: .2rem;
    top: .3rem;
}

.weekBus {
    background-color: #ffcba4;
}

.noResult {
    color: #333333;
    font-size: .30rem;
    text-align: center;
    height: 0.8rem;
    line-height: 0.8rem;
}

.roadPhoto {
    display: inline;
    width: .59rem;
}

.add2 {
    font-size: 0.26rem;
}

.remark {
    line-height: 0.7rem;
}

.addressRemark {
    font-size: .28rem;
    color: #666;
    padding-bottom: .2rem;
}

:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    font-size: 0.32rem !important;
    color: #999!important;
}

::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    font-size: 0.32rem !important;
    color: #999!important;
}

input:-ms-input-placeholder {
    font-size: 0.32rem !important;
    color: #999!important;
}

input::-webkit-input-placeholder {
    font-size: 0.32rem !important;
    color: #999!important;
}

.time2 {
    line-height: .6rem;
}

.xxTime {
    background: url(../../../static/images/query/xiexian@2x.png) no-repeat;
    background-position: .7rem .9rem;
    background-size: .22rem; 
}
</style>


